$(document).ready(() => {
	$('#sidebarCollapse').on('click', function() {
		$('#sidebar').toggleClass('slide');
		$('.overlay').toggleClass('active');
		$('body').toggleClass('lock-scroll');
	});

	$('.closeSideBar').click(() => {
		$('#sidebar').toggleClass('slide');
		$('.overlay').toggleClass('active');
		$('body').toggleClass('lock-scroll');
	});
	// Fancy Navebar Effect on Scroll
	var nav = document.getElementById('topnav');
	var logo_img = document.getElementById('logo-img');
	var ham_img = document.getElementById('ham-img');
	var logo = logo_img.style;
	var ham = ham_img.style;

	window.onscroll = function() {
		// checks if window is scrolled more than 500px, adds/removes solid class
		if (document.body.scrollTop > 150 || document.documentElement.scrollTop > 150) {
			nav.classList.add('nav-color');
			nav.style.padding = '10px 0';
			logo.width = '120px';
			ham.width = '30px';
		} else {
			nav.classList.remove('nav-color');
			nav.style.padding = '15px 0';
			nav.style.transition = '.2s';
			logo.width = '150px';
			logo.transition = '.2s';
			ham.width = '40px';
			ham.transition = '.2s';
		}
	};

	// Owl Carousel For Hero Slider
	//
	var owlone = $('.owlOne');

	owlone.owlCarousel({
		loop: true,
		margin: 0,
		nav: false,
		dots: false,
		items: 1,
		mouseDrag: false,
		touchDrag: false,
		autoplay: true,
		// autoplayTimeout: 2000,
	});

	// Owl Carousel For Team Slider
	//
	var owlTwo = $('.owlTwo');

	owlTwo.owlCarousel({
		loop: true,
		margin: 10,
		nav: false,
		dots: true,
		items: 4,
		autoplay: false,
		// autoplayTimeout: 2000,
		responsive: {
			0: {
				items: 1,
			},

			600: {
				items: 2,
			},

			1000: {
				items: 3,
			},

			1200: {
				items: 4,
			},
		},
	});

	//Applying Active Class On Nav Link when CLicked

	$('#sidebar .nav-link').on('click', function() {
		$('nav.menu')
			.find('.active')
			.removeClass('active');
		$(this)
			.parent('.nav-item')
			.addClass('active');

		if (this.hash !== '') {
			event.preventDefault();
			var hash = this.hash;
			$('html, body').animate(
				{
					scrollTop: $(hash).offset().top - 70,
				},
				1000
			);
		}
	});

	// Text ReadMore (Extra Content Hidden)
	ReadMore('more', 92, 'readMore');
	ReadMore('Tmore', 124, 'readTMore');

	// Gallery Images
	// img-wrap class is used to copy image src to anchore tag
	// href value to activate lightbox
	$('.img-wrap').each(function() {
		$(this).attr(
			'href',
			$(this)
				.find('img')
				.attr('src')
		);
	});
});
